p, h1, h2, h3 {
  margin: 0;
}

ul {
  margin: 0;
  padding-left: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

#card_user {
  border-radius: 20px;
  width: 300px;
  margin: 30px auto;
  box-shadow: -1px 11px 20px #223c5030;
}

.wrapper_img {
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  align-items: center;
  width: 100%;
  height: 290px;
  display: flex;
  overflow: hidden;
}

.wrapper_response {
  background: #fff;
  border-bottom-right-radius: 20px;
  border-bottom-left-radius: 20px;
  padding: 15px 15px 20px;
  position: relative;
}

.rating {
  background-color: #fff;
  border-radius: 10px;
  max-width: max-content;
  padding: 3px 10px;
  font-size: 20px;
  display: inline-block;
  position: absolute;
  top: -15px;
  right: 30%;
  box-shadow: 1px 16px 19px #223c501c;
}

.rating:before {
  content: "★★★★★";
  display: block;
}

.rating_items {
  flex-direction: row-reverse;
  justify-content: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
  top: 3px;
  right: .1px;
  overflow: hidden;
}

.rating_item {
  opacity: 0;
  visibility: hidden;
  width: 0%;
  height: 0%;
  position: absolute;
  top: 0;
  left: 0;
}

.rating_label {
  color: #d7d5d5;
  flex: 0 0 3%;
}

.rating_label:before {
  content: "★";
  transition: color .3s;
  display: block;
}

.rating_item:checked, .rating_item:checked ~ .rating_label {
  color: gold;
}

.name_user {
  margin-top: 5px;
}

.data_add_response {
  color: #9b9ba3;
  font-size: 14px;
}

.user_response {
  height: 95px;
  margin-top: 15px;
  overflow: hidden;
}

.details_response {
  height: 100%;
  overflow: visible;
}

.button_details {
  cursor: pointer;
  color: #7b7b7b;
  background-color: #0000;
  border: none;
  margin: 5px;
}

